a {
    color: #42a5f5;
}

#help-container {
    margin-top: 14px;
}

#help-container div {
    font-family: 'Poppins', sans-serif;
    color: grey;
}

#legendHeader {
    margin-left: 4px;
    margin-top: 10px;
    margin-bottom: 28px;
    text-decoration: underline;
}

.node-symbol {
    width: 24px;
    height: 24px;
    border-radius: 50%;
}

#regular-node {
    background: rgb(100%,72.068787%,0%);
    margin-left: 4px;
}

#regular-node-small {
    width: 12px;
    height: 12px;
    margin-top: 10px;
    background: rgb(100%,72.068787%,0%);
}

#regular-node-medium {
    width: 24px;
    height: 24px;
    background: rgb(100%,72.068787%,0%);
}

#selected-node {
    background-color:rgb(100%,72.068787%,0%);
    border: 4px solid #001182;;    
    height: 24px;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    width: 24;
}

.link-symbol {
    width: 30px;
    height: 4px;
}

#non-highlighted-connection {
    background: #302d2320;
}

#citation-or-reference {
    background: #001182a0;
}

#similarity {
    border-bottom: 4px dotted #bf42f5a0;
    width: 30px;
}

.legend-row {
    display: grid;
    grid-template-columns: 15% 85%;
    grid-template-rows: 100%;
    column-gap: 0%;
    margin-bottom: 16px;
}

.legend-row * {
    justify-self: start;
    align-self: center;
}