.node {
    stroke: #fff;
    stroke-width: 1.5px;
}

.node .selected {
    stroke: #001182;
    stroke-width: 4px;
}

.link {
    stroke: #302d2320;
    stroke-width: 1;
}

.link .citation {
    stroke: #001182a0;
    stroke-width: 4;
}

.link .reference {
    stroke: #001182a0;
    stroke-width: 4;
}

.link .similar {
    stroke: #bf42f5a0;
    stroke-width: 8;
    stroke-dasharray: 10;
}

#force-graph svg {
    display: block;
}

.graph-background {
    fill: #ffffff;
}

.y-axis text {
    fill: #222;
    font-family: 'Poppins', sans-serif;
    font-size : 14px
}

.y-axis line {
    stroke: #ccc
}

.y-axis path {
    stroke: #ccc
}